:root {
  --c-bg: #ffffff;
  --c-white: #ffffff;
  --c-black: #000000;
  --c-grey: #555555;
  --c-light-grey: #888888;
  --c-border: #e0e0e0;
  --c-success: #555555;
  --c-error: #555555;
  --c-warning: #555555;
  --c-blue: #555555;
  --c-purple: #555555;
  --c-teal: #555555;
}
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
body {
  font-family: "Poppins";
  background: var(--c-bg);
  color: var(--c-black);
  line-height: 1.6;
}
.top-bar {
  background: var(--c-black);
  color: var(--c-white);
  padding: 0.6rem 0;
  border-bottom: 1px solid var(--c-black);
  position: sticky;
  top: 0;
  z-index: var(--z-topbar, 100);
}
.top-bar-container {
  max-width: 1400px;
  margin: 0 auto;
  padding: 0 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 0.5rem;
}
.logo {
  color: var(--c-white);
  font-family: "Poppins";
  font-size: 1.2rem;
  font-weight: 600;
  letter-spacing: 0.1em;
  text-decoration: none;
  transition: opacity 0.3s;
}
.logo:hover {
  opacity: 0.8;
}
.logo span {
  font-size: 0.7rem;
  display: block;
  font-weight: 300;
  letter-spacing: 0.2em;
  margin-top: -0.2rem;
}
.user-info {
  display: flex;
  align-items: center;
  gap: 0.75rem;
}
.real-time-indicator {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  font-size: 0.65rem;
  font-weight: 600;
  letter-spacing: 0.1em;
  color: #555555;
}
.pulse-dot {
  width: 6px;
  height: 6px;
  background: #555555;
  border-radius: 50%;
  animation: pulse 2s infinite;
}
@keyframes pulse {
  0%,
  100% {
    opacity: 1;
  }
  50% {
    opacity: 0.5;
  }
}
.user-name {
  font-size: 0.8rem;
  color: var(--c-white);
}
.btn-logout {
  background: transparent;
  color: var(--c-white);
  border: 1px solid var(--c-white);
  padding: 0.3rem 0.8rem;
  cursor: pointer;
  font-size: 0.7rem;
  text-transform: uppercase;
  letter-spacing: 0.1em;
  transition: all 0.3s ease;
}
.btn-logout:hover {
  background: var(--c-white);
  color: var(--c-black);
}
.hamburger {
  display: none;
  flex-direction: column;
  cursor: pointer;
  padding: 0.3rem;
  z-index: var(--z-hamburger-button-alt, 101);
}
.hamburger span {
  width: 20px;
  height: 2px;
  background: var(--c-white);
  margin: 2px 0;
  transition: all 0.3s ease;
  border-radius: 2px;
}
.hamburger.active span:nth-child(1) {
  transform: rotate(45deg) translate(5px, 5px);
}
.hamburger.active span:nth-child(2) {
  opacity: 0;
}
.hamburger.active span:nth-child(3) {
  transform: rotate(-45deg) translate(6px, -6px);
}
.container {
  max-width: 1400px;
  margin: 0 auto;
  padding: 1rem;
}
.page-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
  flex-wrap: wrap;
  gap: 0.5rem;
}
.page-title {
  font-family: "Poppins";
  font-size: 1.2rem;
  font-weight: 400;
  color: var(--c-black);
  letter-spacing: 0.02em;
  margin-bottom: 0.25rem;
}
.page-subtitle {
  font-size: 0.75rem;
  color: var(--c-grey);
}
.time-selector {
  display: flex;
  gap: 0.25rem;
}
.time-btn {
  background: var(--c-white);
  border: 1px solid var(--c-border);
  padding: 0.3rem 0.6rem;
  cursor: pointer;
  font-size: 0.65rem;
  text-transform: uppercase;
  letter-spacing: 0.08em;
  transition: all 0.2s ease;
}
.time-btn:hover {
  border-color: var(--c-black);
}
.time-btn.active {
  background: var(--c-black);
  color: var(--c-white);
  border-color: var(--c-black);
}
.tabs {
  display: flex;
  gap: 0;
  border-bottom: 1px solid var(--c-border);
  margin-bottom: 1rem;
  overflow-x: auto;
  background: var(--c-white);
}
.tab {
  padding: 0.5rem 1rem;
  cursor: pointer;
  font-size: 0.7rem;
  text-transform: uppercase;
  letter-spacing: 0.08em;
  color: var(--c-grey);
  border-bottom: 2px solid transparent;
  transition: all 0.2s ease;
  background: none;
  border-top: none;
  border-left: none;
  border-right: none;
  white-space: nowrap;
}
.tab:hover {
  color: var(--c-black);
  border-bottom-color: var(--c-light-grey);
}
.tab.active {
  color: var(--c-black);
  border-bottom-color: var(--c-black);
  font-weight: 600;
}
.stats-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(140px, 1fr));
  gap: 0.5rem;
  margin-bottom: 1rem;
}
.stat-card {
  background: var(--c-white);
  border: 1px solid var(--c-border);
  border-left: 3px solid var(--c-black);
  padding: 0.75rem;
  transition: all 0.3s ease;
}
.stat-card:hover {
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.08);
  transform: translateY(-1px);
}
.stat-card.blue {
  border-left-color: var(--c-blue);
}
.stat-card.success {
  border-left-color: var(--c-success);
}
.stat-card.purple {
  border-left-color: var(--c-purple);
}
.stat-card.teal {
  border-left-color: var(--c-teal);
}
.stat-card.warning {
  border-left-color: var(--c-warning);
}
.stat-label {
  font-size: 0.65rem;
  text-transform: uppercase;
  letter-spacing: 0.06em;
  color: var(--c-grey);
  margin-bottom: 0.4rem;
}
.stat-value {
  font-family: "Poppins";
  font-size: 1.5rem;
  font-weight: 500;
  color: var(--c-black);
  line-height: 1;
  margin-bottom: 0.3rem;
}
.stat-change {
  font-size: 0.65rem;
  color: var(--c-grey);
}
.stat-change.positive {
  color: var(--c-success);
}
.stat-change.negative {
  color: var(--c-error);
}
.two-col-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  gap: 1rem;
  margin-bottom: 1rem;
}
.three-col-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 1rem;
  margin-bottom: 1rem;
}
.chart-card {
  background: var(--c-white);
  border: 1px solid var(--c-border);
  overflow: hidden;
}
.chart-header {
  padding: 1rem;
  border-bottom: 1px solid var(--c-border);
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.chart-title {
  font-size: 0.9rem;
  font-weight: 400;
  text-transform: uppercase;
  letter-spacing: 0.08em;
  color: var(--c-black);
}
.data-table {
  width: 100%;
  border-collapse: collapse;
}
.data-table thead {
  background: var(--c-bg);
}
.data-table th {
  padding: 0.6rem 1rem;
  text-align: left;
  font-size: 0.7rem;
  text-transform: uppercase;
  letter-spacing: 0.08em;
  color: var(--c-grey);
  font-weight: 400;
  border-bottom: 1px solid var(--c-border);
}
.data-table td {
  padding: 0.8rem 1rem;
  border-bottom: 1px solid var(--c-border);
  font-size: 0.8rem;
}
.data-table tr:hover td {
  background: rgba(0, 0, 0, 0.02);
}
.list-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.6rem 1rem;
  border-bottom: 1px solid var(--c-border);
}
.list-item:last-child {
  border-bottom: none;
}
.list-item-label {
  font-size: 0.75rem;
  color: var(--c-grey);
}
.list-item-value {
  font-family: "Poppins";
  font-size: 1.2rem;
  font-weight: 500;
  color: var(--c-black);
}
.progress-bar {
  height: 6px;
  background: var(--c-border);
  border-radius: 3px;
  overflow: hidden;
  margin-top: 0.3rem;
}
.progress-fill {
  height: 100%;
  background: var(--c-black);
  transition: width 0.3s ease;
}
.progress-fill.blue {
  background: var(--c-blue);
}
.progress-fill.success {
  background: var(--c-success);
}
.progress-fill.purple {
  background: var(--c-purple);
}
.progress-fill.teal {
  background: var(--c-teal);
}
.badge {
  display: inline-block;
  padding: 0.2rem 0.5rem;
  font-size: 0.65rem;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  font-weight: 600;
  border: 1px solid;
}
.badge-desktop {
  background: rgba(0, 102, 204, 0.1);
  color: var(--c-blue);
  border-color: var(--c-blue);
}
.badge-mobile {
  background: rgba(51, 51, 51, 0.1);
  color: var(--c-success);
  border-color: var(--c-success);
}
.badge-tablet {
  background: rgba(184, 134, 11, 0.1);
  color: var(--c-warning);
  border-color: var(--c-warning);
}
.loading-spinner {
  border: 2px solid var(--c-border);
  border-top: 2px solid var(--c-black);
  border-radius: 50%;
  width: 20px;
  height: 20px;
  animation: spin 1s linear infinite;
  margin: 0 auto;
}
@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.loading-state {
  text-align: center;
  padding: 1rem;
  color: var(--c-grey);
}
.error-state {
  background: rgba(139, 0, 0, 0.05);
  border: 1px solid var(--c-error);
  border-left: 3px solid var(--c-error);
  padding: 1rem;
  margin-bottom: 1rem;
}
.error-state h3 {
  color: var(--c-error);
  margin-bottom: 0.5rem;
  font-family: "Poppins";
  font-size: 1rem;
}
.error-state code {
  background: rgba(0, 0, 0, 0.05);
  padding: 0.2rem 0.4rem;
  border-radius: 3px;
  font-family: "Courier New", monospace;
}
.btn {
  background: var(--c-black);
  color: var(--c-white);
  border: none;
  padding: 0.5rem 1rem;
  cursor: pointer;
  font-size: 0.7rem;
  text-transform: uppercase;
  letter-spacing: 0.1em;
  transition: all 0.3s ease;
}
.btn:hover {
  background: var(--c-grey);
}
.btn-secondary {
  background: var(--c-white);
  color: var(--c-black);
  border: 1px solid var(--c-border);
}
.btn-secondary:hover {
  border-color: var(--c-black);
}
@media (max-width: 1024px) {
  .two-col-grid {
    grid-template-columns: 1fr;
  }
  .three-col-grid {
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  }
}
@media (max-width: 768px) {
  .top-bar-container {
    padding: 0 0.5rem;
    position: relative;
  }
  .logo {
    font-size: 1rem;
    flex: 0 0 auto;
  }
  .logo span {
    font-size: 0.6rem;
  }
  .hamburger {
    display: flex;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
  }
  .user-info {
    flex: 0 0 auto;
  }
  .user-name {
    display: none;
  }
  .real-time-indicator {
    display: none;
  }
  .btn-logout {
    padding: 0.3rem 0.6rem;
    font-size: 0.65rem;
  }
  .tabs {
    position: fixed;
    top: 0;
    left: -100%;
    width: 80%;
    max-width: 250px;
    height: 100vh;
    background: var(--c-white);
    flex-direction: column;
    padding: 4rem 0 1rem 0;
    border-bottom: none;
    border-right: 1px solid var(--c-border);
    overflow-y: auto;
    transition: left 0.3s ease;
    z-index: var(--z-mobile-sidebar, 99);
    box-shadow: 2px 0 10px rgba(0, 0, 0, 0.1);
  }
  .tabs.show {
    left: 0;
  }
  .tab {
    padding: 0.8rem 1rem;
    border-bottom: 1px solid var(--c-border);
    border-left: 2px solid transparent;
    white-space: normal;
    text-align: left;
  }
  .tab:hover {
    background: var(--c-bg);
    border-left-color: var(--c-black);
  }
  .tab.active {
    background: var(--c-bg);
    border-left-color: var(--c-black);
    border-bottom-color: var(--c-border);
  }
  .mobile-menu-backdrop {
    display: none;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.5);
    z-index: var(--z-mobile-sidebar-backdrop, 98);
  }
  .mobile-menu-backdrop.show {
    display: block;
  }
  .stats-grid {
    grid-template-columns: 1fr;
  }
  .page-header {
    flex-direction: column;
    align-items: flex-start;
  }
  .time-selector {
    width: 100%;
    overflow-x: auto;
  }
  .container {
    padding: 0.5rem;
  }
  .page-title {
    font-size: 1.2rem;
  }
  .stat-value {
    font-size: 1.2rem;
  }
  .two-col-grid,
  .three-col-grid {
    grid-template-columns: 1fr;
  }
  .data-table {
    font-size: 0.7rem;
  }
  .data-table th,
  .data-table td {
    padding: 0.5rem 0.4rem;
    font-size: 0.65rem;
  }
}
@media (max-width: 480px) {
  .logo {
    font-size: 0.9rem;
  }
  .page-title {
    font-size: 1rem;
  }
  .stat-value {
    font-size: 1.1rem;
  }
}
